export const newPostPath = () => '/new';
export const editPostPath = (id = ':id') => `/posts/${id}/edit`;
export const postsPath = (id = ':id') => `/posts/${id}`;
export const rootPath = () => '/';
export const aboutPath = () => '/about';
export const contactPath = () => '/contact';



